import MapSection from "../components/main/MapSection";
import deliverySvg from '../img/icons/delivery.svg'
import locationSvg from '../img/icons/location.svg'
import Counter from "../components/subComponents/Counter";
export default function Delivery({head}){
    /*
    * props->{head - }
      .
    * */
    return <>
        <section className="sections">
            <h1 className="sections__head">{head}</h1>
        </section>
        <address className="send-info">
            <h5>
                <img src={locationSvg}/>
                    <span>    : , . , .  290.</span>
            </h5>
        </address>
        <MapSection/>
        <section className="sections">
            <address className="send-info">
                <h5>
                    <img src={deliverySvg}/>
                    <span>   </span>
                </h5>
                <p>
                         :  , , ,  
                    
                     .         .
                </p>
            </address>
        </section>

        <form className="sections form form_with-frame sections_head-left" action="">
            <div className="form__header form__header_while">
                <h2 className="sections__head">-  </h2>
            </div>
            <div className="form__body form__body_text-aria-left">
                <label className="form__field">
                    <input type="text" name="city-send" placeholder=" " title=" "/>
                </label>
                <label className="form__field">
                    <input type="text" name="city-get" placeholder=" " title=" "/>
                </label>
                <div className="form__field">
                    <p>:</p>
                    <Counter name={'weight'} placeholder={''} mainClass={'form__amt'}/>

                </div>
                <div className="form__field">
                    <p>:</p>
                    <Counter name={'long'} placeholder={''} mainClass={'form__amt'}/>
                </div>
                <div className="form__field">
                    <p>:</p>
                    <Counter name={'wide'} placeholder={""} mainClass={'form__amt'}  />
                </div>
                <div className="form__field">
                    <p>:</p>
                    <Counter name={'height'} placeholder={""} mainClass={'form__amt'}/>

                </div>
                <div className="form__field form__field_radio">
                    <input id="delivery-to-point-issue-orders" type="checkbox" name="type_order" value="visitor"/>
                        <label htmlFor="delivery-to-point-issue-orders">     </label>
                </div>
                <div className="form__field form__field_radio">
                    <input id="delivery-by-courier-to-door" type="checkbox" name="type_order" value="visitor"/>
                        <label htmlFor="delivery-by-courier-to-door">    </label>
                </div>
            </div>
            <div className="form__footer">
                <input type="submit" className="blow-button" title="" value={''}/>
                    <a href="#" className="form__text-smail">   ,   
                        .</a>
            </div>
        </form>

    </>
}